import {combineEpics, Epic, ofType} from "redux-observable";
import {RootState} from "../store";
import {catchError, from, map, mergeMap} from "rxjs";
import {authActions} from "./auth.actions";
import {client} from "../../graphQL/client";
import {User} from "../../graphQL/modules/users/users.types";
import {
    AUTH_LOGIN_MUTATION,
    AuthLoginData,
    AuthLoginInputType,
    AuthLoginVars
} from "../../graphQL/modules/auth/auth.mutations";

export const fetchCreateTodosEpic: Epic<ReturnType<typeof authActions.userLoginAsync>, any, RootState> = (action$, state$) =>
    action$.pipe(
        ofType('LOGIN_USER_ASYNC'),
        mergeMap(action =>
            from(client.mutate<AuthLoginData, AuthLoginVars>({
                mutation: AUTH_LOGIN_MUTATION,
                variables: {authLoginInputType: action.payload.credentials}
            })).pipe(
                map(response => {
                    console.log(response)
                    authActions.setAuthedUser(response.data?.auth.user as User, response.data?.auth.token as string)
                })
            )
        )
    );

// @ts-ignore
export const authEpics = combineEpics(fetchCreateTodosEpic)